export function getPersistedState(scripID){

  var seg1 = `https://mihika-backend.herokuapp.com/state/${scripID}`;

  
  return function(dispatch) {
        axios({
         method: 'get',
         url: seg1,
        })
        .then(response => {
          dispatch({type: 'PERSISTED_STATE_DATA_OBTAINED',payload: response.data});
        })
        .catch((error) => {
          dispatch({type: 'PERSISTED_STATE_DATA_ERROR',payload: 'Error'});
          console.log("Error Response"+error);
        });
  };
  
} 

