export function postPersistedState(data){
  var url = 'https://mihika-backend.herokuapp.com/state';
  return function(dispatch) {
    axios.post(url, data)
    .then(response => {
      dispatch({type: 'POST_PERSISTED_STATE_DATA_SUCCESS',payload: response.data});
    })
    .catch((error) => {
      dispatch({type: 'POST_PERSISTED_STATE_DATA_ERROR',payload: 'Error'});
      console.log("Error Response"+error);
    });
  };
} 

export function postPersistedProps(data){
  var url = 'https://mihika-backend.herokuapp.com/items';
  const size = encodeURI(JSON.stringify(data)).split(/%..|./).length - 1;
  console.log('ALERT....Size of props stored in db is...' + bytesToSize(size)); 
  return function(dispatch) {
    axios.post(url, data)
    .then(response => {
      dispatch({type: 'POST_PERSISTED_PROPS_DATA_SUCCESS',payload: response.data});
    })
    .catch((error) => {
      dispatch({type: 'POST_PERSISTED_PROPS_DATA_ERROR',payload: 'Error'});
      console.log("Error Response"+error);
    });
  };
}





