
import _ from "lodash";
import cjson from 'compressed-json';

export default function(state = [], action) {
 
  switch(action.type) {
    case 'PERSISTED_STATE_DATA_OBTAINED':
      if(!action.payload) {
        return []
      }
      const persistedStateData = action.payload && cjson.decompress(action.payload.state);
      var newstate2 = state.concat(persistedStateData);                
      return newstate2 ;
    default:
      return state;
    }
}